export default function Index() {
  const [showPortfolio, setShowPortfolio] = React.useState(false);
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  return <React.StrictMode>
    <Helmet>
      <title>Tamara Nagy Portfolio</title>
    </Helmet>
    <StyledEngineProvider injectFirst>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <AppBar position="static" elevation={0} style={{ backgroundColor: "#282727" }}>
          <Toolbar>
            <Box sx={{ display: { xs: 'flex', sm: 'none' } }}>
              <IconButton onClick={handleOpenNavMenu}>
                <FontAwesomeIcon icon={faBars} color="white" />
              </IconButton>
              <Menu
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'left',
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: 'block', sm: 'none' },
                }}
              >
                <MenuItem onClick={() => { setShowPortfolio(false); handleCloseNavMenu(); ReactGA.pageview("page: About"); }}>
                  <Typography textAlign="center" color="#282727" style={{ fontSize: "1.5rem" }}>ABOUT</Typography>
                </MenuItem>
                <MenuItem onClick={() => { setShowPortfolio(true); handleCloseNavMenu(); ReactGA.pageview("page: Portfolio"); }}>
                  <Typography textAlign="center" color="#282727" style={{ fontSize: "1.5rem" }}>PORTFOLIO</Typography>
                </MenuItem>
              </Menu>
            </Box>

            <Box sx={{ display: { xs: 'none', sm: 'flex' } }}>
              <Button onClick={() => { setShowPortfolio(false); ReactGA.pageview("page: About"); }}>
                <Typography textAlign="center" color="white" style={{ fontSize: "2rem" }}>ABOUT</Typography>
              </Button>
              <Button onClick={() => { setShowPortfolio(true); ReactGA.pageview("page: Portfolio"); }}>
                <Typography textAlign="center" color="white" style={{ fontSize: "2rem" }}>PORTFOLIO</Typography>
              </Button>
            </Box>

            <div style={{ flexGrow: 1 }} />

            <IconButton onClick={() => { ReactGA.outboundLink({ label: "LinkedIn" }, () => { }); window && window.open('https://www.linkedin.com/in/tajuna'); }}>
              <FontAwesomeIcon icon={faLinkedin} color="white" />
            </IconButton>
            <IconButton onClick={() => { ReactGA.outboundLink({ label: "e-mail" }, () => { }); window && window.open('mailto: nagytamara93@gmail.com'); }}>
              <FontAwesomeIcon icon={faEnvelope} color="white" />
            </IconButton>
            <IconButton onClick={() => { ReactGA.outboundLink({ label: "CV-pdf" }, () => { }); window && window.open(tamiCv); }} style={{ paddingBottom: "1.2rem" }} >
              <Typography fontFamily="Sansita" color="textPrimary" lineHeight="2.4rem"
                style={{ fontSize: "2.4rem" }}>CV</Typography>
            </IconButton>
          </Toolbar>
        </AppBar>
        {showPortfolio ? <Projects /> : <About setShowPortfolio={setShowPortfolio} />}
      </ThemeProvider>
    </StyledEngineProvider>
  </React.StrictMode >;
}

function About({ setShowPortfolio }) {
  return <Container maxWidth="md" disableGutters
    style={{ marginTop: "4rem" }}>
    <Grid container align="center" justifyContent="center">
      <Grid item xs={12} md={4}>
        <img src={profile} alt="" style={{
          borderRadius: "50%",
          width: "auto", height: "auto",
          maxWidth: "65%", maxHeight: "20vh"
        }} />
      </Grid>
      <Grid container xs={10} md={8} alignContent="center">
        <Box sx={{ display: { sm: "block", md: "none" } }} height="2rem" />
        <Typography style={{ fontSize: "2rem" }} textAlign="justify">
          Hello, my name is <span style={{ color: "#5FB524" }}>Tamara Nagy</span>, I'm a creative web designer living in Espoo.
          I have two years experience designing creatives for various online interfaces.
          I'm interested in web design, digital design, and illustration.
          <br />
          Check out my work on the <a href="#" style={{ color: "#5FB524", textDecoration: "none" }} onClick={() => { setShowPortfolio(true) }}>portfolio</a> page!
        </Typography>
      </Grid>
    </Grid>
  </Container>;
}

function Projects() {
  const [projects, setProjects] = React.useState("ui-design");

  const [open, setOpen] = useState(false);
  const [content, setContent] = useState(null);

  const show = contentToShow => {
    setContent(contentToShow);
    setOpen(true);
  };

  return <><Container maxWidth="md"
    style={{ marginTop: "4rem" }}>
    <TabContext value={projects}>
      <Tabs value={projects} onChange={(event, newValue) => { setProjects(newValue); ReactGA.pageview("tab: " + newValue) }} centered textColor="inherit">
        <Tab value="ui-design" label="UI design" style={{ fontSize: "2rem" }} />
        <Tab value="marketing" label="Marketing" style={{ fontSize: "2rem" }} />
        <Tab value="logos" label="Logos" style={{ fontSize: "2rem" }} />
        <Tab value="illustration" label="Illustration" style={{ fontSize: "2rem" }} />
      </Tabs>
      <TabPanel value="ui-design">
        <ImageList gap={10}>
          <ButtonBase onClick={() => show(japan)}>
            <ImageListItem>
              <img src={japan0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(weather)}>
            <ImageListItem>
              <img src={weather0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(catwear)}>
            <ImageListItem>
              <img src={catwear0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(sebeszem)}>
            <ImageListItem>
              <img src={sebeszem0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(toolapp)}>
            <ImageListItem>
              <img src={toolapp0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(donut)}>
            <ImageListItem>
              <img src={donut0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(berlinger)}>
            <ImageListItem>
              <img src={berlinger0} alt="" />
            </ImageListItem>
          </ButtonBase>
        </ImageList>
      </TabPanel>
      <TabPanel value="marketing">
        <ImageList>
          <ButtonBase onClick={() => show(newsletters)}>
            <ImageListItem>
              <img src={newsletter0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(banners)}>
            <ImageListItem>
              <img src={banner0} alt="" />
            </ImageListItem>
          </ButtonBase>
        </ImageList>
      </TabPanel>
      <TabPanel value="logos">
        <ImageList>
          <ButtonBase onClick={() => show(bacsContent)}>
            <ImageListItem>
              <img src={bacs0} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(palocContent)}>
            <ImageListItem>
              <img src={paloc0} alt="" />
            </ImageListItem>
          </ButtonBase>
        </ImageList>
      </TabPanel>
      <TabPanel value="illustration">
        <ImageList>
          <ButtonBase onClick={() => show(forestContent)}>
            <ImageListItem>
              <img src={forestThumbnail} alt="" />
            </ImageListItem>
          </ButtonBase>
          <ButtonBase onClick={() => show(trashcatContent)}>
            <ImageListItem>
              <img src={trashcatThumbnail} alt="" />
            </ImageListItem>
          </ButtonBase>
        </ImageList>
      </TabPanel>
    </TabContext>
  </Container>
    <Dialog fullWidth maxWidth="md"
      open={open} onClose={() => setOpen(false)} onClick={() => setOpen(false)}>
      <DialogContent style={{ backgroundColor: "black" }}>
        {content}
      </DialogContent>
    </Dialog>
  </>;
}

